import React from "react";
import ReactDOM from "react-dom";
import {
  AdaptivityProvider,
  ConfigProvider,
  useAdaptivity,
  AppRoot,
  SplitLayout,
  SplitCol,
  ViewWidth,
  View,
  Panel,
  PanelHeader,
  Header,
  Group,
  SimpleCell,
  FixedLayout,
  PromoBanner
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

const promoBannerProps = {
  title: 'Заголовок',
  domain: 'vk.com',
  trackingLink: 'https://vk.com',
  ctaText: 'Перейти',
  advertisingLabel: 'Реклама',
  iconLink: 'https://sun9-7.userapi.com/c846420/v846420985/1526c3/ISX7VF8NjZk.jpg',
  description: 'Описание рекламы',
  ageRestrictions: "14+",
  statistics: [
    { url: '', type: 'playbackStarted' },
    { url: '', type: 'click' }
  ]
};

 
const App = () => {
  const { viewWidth } = useAdaptivity();
 
  return (
    <AppRoot>
      <SplitLayout header={<PanelHeader separator={false} />}>
        <SplitCol spaced={viewWidth && viewWidth > ViewWidth.MOBILE}>
          <View activePanel="promo">
  <Panel id="promo">
    <FixedLayout vertical="top">
      <PromoBanner bannerData={promoBannerProps} />
    </FixedLayout>
  </Panel>
</View>
        </SplitCol>
      </SplitLayout>
    </AppRoot>
  );
};
 
 bridge.send("VKWebAppInit", {});


 
ReactDOM.render(
  <ConfigProvider>
    <AdaptivityProvider>
      <App />
    </AdaptivityProvider>
  </ConfigProvider>,
  document.getElementById("root")
);
function print_r(theObj){
		if(theObj.constructor == Array ||theObj.constructor == Object){
			document.write("<ul>")
			for(var p in theObj){
				if(theObj[p].constructor == Array||theObj[p].constructor == Object){
					document.write("<li>["+p+"] => "+typeof(theObj)+"</li>");
					document.write("<ul>")
					print_r(theObj[p]);
					document.write("</ul>")
				} 
				else{
					alert("<li>["+p+"] => "+theObj[p]+"</li>");
				}
			}
			document.write("</ul>")
		}
	}
